const manager = {
    handleSong(song) {
        let recSong = {
            id: song.id,
            album: song.al,
            name: song.name,
            artist: song.ar,
        }
        return recSong;
    },
    unique(arr) {
        let unique = {};
        arr.forEach(function(item) {
            unique[JSON.stringify(item)] = item; //
        })
        var arr = Object.keys(unique).map(function(u) {
            //Object.keys()map.unique
            return JSON.parse(u);
        });
        return arr;
    },
    setLocalList(list) {
        let localList = JSON.stringify(list);
        localStorage.setItem('localList', localList);
    },
    setLocalSongs(songs) {
        let localSongs = JSON.stringify(songs);
        localStorage.setItem('localSongs', localSongs);
    },
}

export default manager;


